<template>
<div :style='{"width":"100%","padding":"30px 7% 40px","margin":"0px auto","position":"relative","background":"#fff"}'>
    <el-form
	  :style='{"border":"0px solid #eee","padding":"30px 0","alignItems":"flex-start","flexWrap":"wrap","background":"#fff","display":"flex","width":"100%","position":"relative"}'
      class="add-update-preview"
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="120px"
    >
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="" prop="xueshengzhanghao">
            <el-input v-model="ruleForm.xueshengzhanghao" 
                placeholder="" clearable :disabled=" false  ||ro.xueshengzhanghao"></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="" prop="xueshengxingming">
            <el-input v-model="ruleForm.xueshengxingming" 
                placeholder="" clearable :disabled=" false  ||ro.xueshengxingming"></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="" prop="xingbie">
            <el-input v-model="ruleForm.xingbie" 
                placeholder="" clearable :disabled=" false  ||ro.xingbie"></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="" prop="xueli">
            <el-input v-model="ruleForm.xueli" 
                placeholder="" clearable :disabled=" false  ||ro.xueli"></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="" prop="xuekemenlei">
            <el-input v-model="ruleForm.xuekemenlei" 
                placeholder="" clearable :disabled=" false  ||ro.xuekemenlei"></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="" prop="shouji">
            <el-input v-model="ruleForm.shouji" 
                placeholder="" clearable :disabled=" false  ||ro.shouji"></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="" prop="shenfenzheng">
            <el-input v-model="ruleForm.shenfenzheng" 
                placeholder="" clearable :disabled=" false  ||ro.shenfenzheng"></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="" prop="qiyezhanghao">
            <el-input v-model="ruleForm.qiyezhanghao" 
                placeholder="" clearable :disabled=" false  ||ro.qiyezhanghao"></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="" prop="qiyemingcheng">
            <el-input v-model="ruleForm.qiyemingcheng" 
                placeholder="" clearable :disabled=" false  ||ro.qiyemingcheng"></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="" v-if="type!='cross' || (type=='cross' && !ro.zhaopinfengmian)" prop="zhaopinfengmian">
            <file-upload
            tip=""
            action="file/upload"
            :limit="3"
            :multiple="true"
            :fileUrls="ruleForm.zhaopinfengmian?ruleForm.zhaopinfengmian:''"
            @change="zhaopinfengmianUploadChange"
            ></file-upload>
          </el-form-item>
            <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' class="upload" v-else label="" prop="zhaopinfengmian">
                <img v-if="ruleForm.zhaopinfengmian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.zhaopinfengmian.split(',')[0]" width="100" height="100">
                <img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.zhaopinfengmian.split(',')" :src="baseUrl+item" width="100" height="100">
            </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="" prop="tongzhibiaoti">
            <el-input v-model="ruleForm.tongzhibiaoti" 
                placeholder="" clearable :disabled=" false  ||ro.tongzhibiaoti"></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="" prop="tongzhishijian">
              <el-date-picker
				  :disabled=" false  ||ro.tongzhishijian"
                  value-format="yyyy-MM-dd HH:mm:ss"
                  v-model="ruleForm.tongzhishijian" 
                  type="datetime"
                  placeholder="">
              </el-date-picker>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}'  label="" prop="tongzhizhuangtai">
            <el-select v-model="ruleForm.tongzhizhuangtai" placeholder="" :disabled=" false  ||ro.tongzhizhuangtai" >
              <el-option
                  v-for="(item,index) in tongzhizhuangtaiOptions"
                  :key="index"
                  :label="item"
                  :value="item">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="" prop="tongzhixiangqing">
            <editor 
                :style='{"minHeight":"250px","padding":"0","margin":"0","borderColor":"#1abc9e30","backgroundColor":"none","borderRadius":"0","borderWidth":"0px","width":"100%","borderStyle":"solid","height":"auto"}'
                v-model="ruleForm.tongzhixiangqing" 
                class="editor" 
                action="file/upload">
            </editor>
          </el-form-item>

      <el-form-item :style='{"width":"100%","padding":"0","margin":"0","textAlign":"center"}'>
        <el-button :style='{"border":"0px solid #eccc19","cursor":"pointer","padding":"0","margin":"0 20px 0 0","color":"#fff","borderRadius":"20px","background":"#0062a5","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}'  type="primary" @click="onSubmit"></el-button>
        <el-button :style='{"border":"1px solid #ccc","cursor":"pointer","padding":"0","margin":"0","outline":"none","color":"#666","borderRadius":"20px","background":"none","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}' @click="back()"></el-button>
      </el-form-item>
    </el-form>
</div>
</template>

<script>
  export default {
    data() {
	  let self = this
      return {
        id: '',
        baseUrl: '',
        ro:{
				xueshengzhanghao : false,
				xueshengxingming : false,
				xingbie : false,
				xueli : false,
				xuekemenlei : false,
				shouji : false,
				shenfenzheng : false,
				qiyezhanghao : false,
				qiyemingcheng : false,
				zhaopinfengmian : false,
				tongzhibiaoti : false,
				tongzhishijian : false,
				tongzhizhuangtai : false,
				tongzhixiangqing : false,
        },
        type: '',
        userTableName: localStorage.getItem('UserTableName'),
        ruleForm: {
          xueshengzhanghao: '',
          xueshengxingming: '',
          xingbie: '',
          xueli: '',
          xuekemenlei: '',
          shouji: '',
          shenfenzheng: '',
          qiyezhanghao: '',
          qiyemingcheng: '',
          zhaopinfengmian: '',
          tongzhibiaoti: '',
          tongzhishijian: '',
          tongzhizhuangtai: '',
          tongzhixiangqing: '',
        },
        tongzhizhuangtaiOptions: [],


        rules: {
          xueshengzhanghao: [
          ],
          xueshengxingming: [
          ],
          xingbie: [
          ],
          xueli: [
            { required: true, message: '', trigger: 'blur' },
          ],
          xuekemenlei: [
          ],
          shouji: [
          ],
          shenfenzheng: [
          ],
          qiyezhanghao: [
          ],
          qiyemingcheng: [
          ],
          zhaopinfengmian: [
          ],
          tongzhibiaoti: [
          ],
          tongzhishijian: [
          ],
          tongzhizhuangtai: [
          ],
          tongzhixiangqing: [
          ],
        },
		centerType: false,
      };
    },
    computed: {



    },
    components: {
    },
    created() {
		if(this.$route.query.centerType){
			this.centerType = true
		}
	  //this.bg();
      let type = this.$route.query.type ? this.$route.query.type : '';
      this.init(type);
      this.baseUrl = this.$config.baseUrl;
      this.ruleForm.tongzhishijian = this.getCurDateTime()
    },
    methods: {
      getMakeZero(s) {
          return s < 10 ? '0' + s : s;
      },
      // 
      download(file){
        window.open(`${file}`)
      },
      // 
      init(type) {
        this.type = type;
        if(type=='cross'){
          var obj = JSON.parse(localStorage.getItem('crossObj'));
          for (var o in obj){
            if(o=='xueshengzhanghao'){
              this.ruleForm.xueshengzhanghao = obj[o];
              this.ro.xueshengzhanghao = true;
              continue;
            }
            if(o=='xueshengxingming'){
              this.ruleForm.xueshengxingming = obj[o];
              this.ro.xueshengxingming = true;
              continue;
            }
            if(o=='xingbie'){
              this.ruleForm.xingbie = obj[o];
              this.ro.xingbie = true;
              continue;
            }
            if(o=='xueli'){
              this.ruleForm.xueli = obj[o];
              this.ro.xueli = true;
              continue;
            }
            if(o=='xuekemenlei'){
              this.ruleForm.xuekemenlei = obj[o];
              this.ro.xuekemenlei = true;
              continue;
            }
            if(o=='shouji'){
              this.ruleForm.shouji = obj[o];
              this.ro.shouji = true;
              continue;
            }
            if(o=='shenfenzheng'){
              this.ruleForm.shenfenzheng = obj[o];
              this.ro.shenfenzheng = true;
              continue;
            }
            if(o=='qiyezhanghao'){
              this.ruleForm.qiyezhanghao = obj[o];
              this.ro.qiyezhanghao = true;
              continue;
            }
            if(o=='qiyemingcheng'){
              this.ruleForm.qiyemingcheng = obj[o];
              this.ro.qiyemingcheng = true;
              continue;
            }
            if(o=='zhaopinfengmian'){
              this.ruleForm.zhaopinfengmian = obj[o].split(",")[0];
              this.ro.zhaopinfengmian = true;
              continue;
            }
            if(o=='tongzhibiaoti'){
              this.ruleForm.tongzhibiaoti = obj[o];
              this.ro.tongzhibiaoti = true;
              continue;
            }
            if(o=='tongzhishijian'){
              this.ruleForm.tongzhishijian = obj[o];
              this.ro.tongzhishijian = true;
              continue;
            }
            if(o=='tongzhizhuangtai'){
              this.ruleForm.tongzhizhuangtai = obj[o];
              this.ro.tongzhizhuangtai = true;
              continue;
            }
            if(o=='tongzhixiangqing'){
              this.ruleForm.tongzhixiangqing = obj[o];
              this.ro.tongzhixiangqing = true;
              continue;
            }
          }
        }else if(type=='edit'){
			this.info()
		}
        // 
        this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            var json = res.data.data;
            if((json.qiyezhanghao!=''&&json.qiyezhanghao) || json.qiyezhanghao==0){
                this.ruleForm.qiyezhanghao = json.qiyezhanghao;
				this.ro.qiyezhanghao = true;
            }
            if((json.qiyemingcheng!=''&&json.qiyemingcheng) || json.qiyemingcheng==0){
                this.ruleForm.qiyemingcheng = json.qiyemingcheng;
				this.ro.qiyemingcheng = true;
            }
          }
        });
        this.tongzhizhuangtaiOptions = ",".split(',')

		if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
			localStorage.removeItem('raffleType')
			setTimeout(() => {
				this.onSubmit()
			}, 300)
		}
      },

    // 
      // 
      info() {
        this.$http.get(`mianshixinxi/detail/${this.$route.query.id}`, {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            this.ruleForm = res.data.data;
          }
        });
      },
      // 
      onSubmit() {
			//
			var crossuserid;
			var crossrefid;
			var crossoptnum;
			this.$refs["ruleForm"].validate(valid => {
				if(valid) {
					if(this.type=='cross'){
						var statusColumnName = localStorage.getItem('statusColumnName');
						var statusColumnValue = localStorage.getItem('statusColumnValue');
						if(statusColumnName && statusColumnName!='') {
							var obj = JSON.parse(localStorage.getItem('crossObj'));
							if(!statusColumnName.startsWith("[")) {
								for (var o in obj){
									if(o==statusColumnName){
										obj[o] = statusColumnValue;
									}
								}
								var table = localStorage.getItem('crossTable');
								this.$http.post(table+'/update', obj).then(res => {});
							} else {
								crossuserid=Number(localStorage.getItem('frontUserid'));
								crossrefid=obj['id'];
								crossoptnum=localStorage.getItem('statusColumnName');
								crossoptnum=crossoptnum.replace(/\[/,"").replace(/\]/,"");
							}
						}
					}
					if(crossrefid && crossuserid) {
						this.ruleForm.crossuserid=crossuserid;
						this.ruleForm.crossrefid=crossrefid;
						var params = {
							page: 1,
							limit: 10,
							crossuserid:crossuserid,
							crossrefid:crossrefid,
						}
						this.$http.get('mianshixinxi/list', {
							params: params
						}).then(res => {
							if(res.data.data.total>=crossoptnum) {
								this.$message({
									message: localStorage.getItem('tips'),
									type: 'error',
									duration: 1500,
								});
								return false;
							} else {
								// 


								this.$http.post(`mianshixinxi/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(res => {
									if (res.data.code == 0) {
										this.$message({
											message: '',
											type: 'success',
											duration: 1500,
											onClose: () => {
												this.$router.go(-1);
											}
										});
									} else {
										this.$message({
											message: res.data.msg,
											type: 'error',
											duration: 1500
										});
									}
								});
							}
						});
					} else {


						this.$http.post(`mianshixinxi/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(res => {
							if (res.data.code == 0) {
								this.$message({
									message: '',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.$router.go(-1);
									}
								});
							} else {
								this.$message({
									message: res.data.msg,
									type: 'error',
									duration: 1500
								});
							}
						});
					}
				}
			});
		},
		// uuid
		getUUID () {
			return new Date().getTime();
		},
		// 
		back() {
			this.$router.go(-1);
		},
      zhaopinfengmianUploadChange(fileUrls) {
          this.ruleForm.zhaopinfengmian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
      },
    }
  };
</script>

